@import "https://fonts.googleapis.com/css2?family=Orbitron&display=swap";
.Button {
  width: 30px;
  height: 30px;
  border: 4px solid #7b7b7b;
  border-color: #fff #7b7b7b #7b7b7b #fff;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  display: flex;
}

.Button:active {
  border-color: #7b7b7b #fff #fff #7b7b7b;
}

.Button.visible {
  border-width: 1px;
  border-color: #7b7b7b;
}

.Button.value-1 {
  color: #00f;
}

.Button.value-2 {
  color: green;
}

.Button.value-3 {
  color: red;
}

.Button.value-4 {
  color: purple;
}

.Button.value-5 {
  color: maroon;
}

.Button.value-6 {
  color: #40e0d0;
}

.Button.value-7 {
  color: #000;
}

.Button.value-8 {
  color: gray;
}

.NumberDisplay {
  color: red;
  width: 80px;
  height: 48px;
  text-align: center;
  background: #000;
  font-family: Orbitron;
  font-size: 30px;
}

.App {
  background-color: #c2c2c2;
  border: 4px solid #999;
  border-color: #fff #999 #999 #fff;
  padding: 16px;
}

.Header {
  background: silver;
  border: 4px solid #fff;
  border-color: #7b7b7b #fff #fff #7b7b7b;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  display: flex;
}

.Body {
  border: 4px solid #fff;
  border-color: #7b7b7b #fff #fff #7b7b7b;
  grid-template-rows: repeat(9, 1fr);
  grid-template-columns: repeat(9, 1fr);
  margin-top: 16px;
  display: grid;
}

.Face {
  width: 52px;
  height: 52px;
  cursor: pointer;
  border: 4px solid #7b7b7b;
  border-color: #fff #7b7b7b #7b7b7b #fff;
  justify-content: center;
  align-items: center;
  font-size: 35px;
  display: flex;
}

.Face:active {
  border-color: #7b7b7b #fff #fff #7b7b7b;
}

* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

